<template>
    <div class="main">
         <van-nav-bar
            left-arrow
            @click-left="onClickLeft"
            @click-right="onClickRight"
            :border='false'
         > 
           <template #left>
               <!-- bounceInLeft -->
                <span ref="wangyiyunyinle" class="iconfont icon-wangyiyunyinle animate__animated"></span>
           </template>
           <template #title>
               <div class="mytitle">
                   <span v-for="(item,index) in titleObj" :key="index" :class="{'attr' : item.attr}" @click="goRouter(item)">{{item.title}}</span>
               </div>
           </template>
           <template #right>
               <span class="iconfont icon-fangda"></span>
           </template>
         </van-nav-bar>

       <keep-alive include="Find">
                <router-view></router-view>
       </keep-alive>
         

        
    </div>
</template>

<script>
    import Flash from '../components/Flash.vue'
    import '../assets/less/Main.less'
    export default {
        name : 'Main',
        data(){
            return{
                titleObj : [
                    {title:'我的',attr:false,name:'Me',path:'/me'},
                    {title:'发现',attr:true ,name:'Find',path:'/find'},
                    {title:'视频',attr:false,name:'Video',path:'/video'},
                ]
            }
        },
        methods:{
            onClickLeft(){

            },
            onClickRight(){
                this.$router.push('Seek')
            },
            goRouter(item){
                  if(item.attr){
                      return;
                  }
                  for(let i=0;i<this.titleObj.length;i++){
                      if(this.titleObj[i].attr){
                             this.titleObj[i].attr = false;
                             break; 
                      }
                  }

                  item.attr = true;
                  this.$router.push({name:item.name})
            },
            
        },
        created(){
           
            for(let i=0;i<this.titleObj.length;i++){
                if(this.titleObj[i].path == this.$route.path){
                        this.titleObj[i].attr = true;
                      
                }else{
                         this.titleObj[i].attr = false;
                }
            }
       
              
        },
        mounted(){
             this.$refs.wangyiyunyinle.classList.add('animate__bounceInLeft');
             setTimeout(()=>{
                 this.$refs.wangyiyunyinle.style.color = '#ff4338'
             },800)
             setTimeout(()=>{
                 this.$refs.wangyiyunyinle.classList.remove('animate__bounceInLeft');
             },1000)

        },
        components:{
            Flash
        }
    }
</script>

<style lang="less" scoped>
 
</style>